#intro
{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1%;
    overflow: hidden;
}
.bg
{
    position: absolute;
    top: 8rem;
    left: 55%;
    right:0;
    z-index: -1;
    object-fit: cover;
    display: flex;
    height: 500px;
    width: 500px;
    border-radius:60%;
    background-size: cover;

}
.content
{
    height: 100vh;
    width: 100vh;
    padding: 3rem;
    font-size: 3rem;
    font-weight: 800;
    flex-direction: column;
    justify-content: center;
    right: 60%;
}
.hello
{   font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.75rem;
    font-weight: 600;
}
.name
{
    color: yellow;
}
.pera
{   margin-top: 1%;
    font-size: .9rem;
    font-weight: 400;
    letter-spacing: 0.65px;
}
.btn
{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    cursor: pointer;
     
}
.btn:hover
{
    background: yellow;
}
.hire
{
    font-weight:bold ;
    
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
@media screen and (max-width:450px) {
    .bg
    {
        right: -20vw;
    }

}
