.navbar
{   background: rgb(26, 30, 49);
    align-items: center;
    height: 6rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    justify-content:center;
 



}
.menuitem
{
    margin: 1.5rem;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 500;
}
.menuitem:hover
{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom:3px solid yellow ;
    padding-top: 0.5rem;
    border-top: 3px solid yellow;

}