#aboutme
{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.iam
{
    font-size: 3rem;
    font-weight: 700;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.description
{
    font-weight: 400;
    font-size: 1.1rem;
    max-width: 51rem;
    padding: 0 2rem;
}
.skilbars
{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.skilbar
{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background:rgb(30, 60, 80);
}
.skilbarimg
{
    object-fit: cover;
    height: 3rem;
    width: 5rem;
    margin-right: 2rem;
}
.skilbarTxt>p
{
    font-size: 0.9rem;
    font-weight: 400;
    color: darkgray;
}
.java
{
    color: yellow;
}