.whatsapp{
    background-image: url(whatsapp.png);
    height: 85px;
    width: 85px;
    border-radius: 50%;
    background-position: center;
    background-size: cover;
    margin: 0px 10px;
}
.whatsapp:hover{
    transform:scale(1.1);
    z-index: 2;
  }
  
