.social{
    display: flex;
    flex-wrap: wrap;
    margin: 0px 100px;
    align-items: center;
    margin-left: 34%;
  }
.contitle
{
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    margin-top: 4rem;
    margin-bottom: 1rem;
}
.perac
{
   font-size: medium; 
   margin-top: 1rem;
   margin-bottom: 2rem;
}
.form{
    margin-left: 16rem;

}
.fillform
{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;

}
.nam ,.email,.msg,.msge
{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(50, 50, 80);
}
.btnsub
{
    margin: 2rem 0;
    padding: 1rem 1.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: medium;
    font-weight: 500;
    cursor: pointer;

}
.btnsub:hover
{
    background: yellow;
    transform: translate(2.1);
}